.modal {
    background-color: white;
    // margin-top: 20px;
    // margin-bottom: 10px;
    // min-height: 581px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
   
    border-radius: 10px;




    @media screen and (min-width:768px) {
        max-width: 584px;
        max-height: 644px;

    }

}

.close-button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--white-color);
    position: absolute;
    right: 8px;
    top: 8px;
    padding: 0;
    margin: 0;
    cursor: pointer;
    transition: fill, border;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    outline: transparent;
    border: none;
    &:focus,&:hover {
     fill: #F8D24E;   
    }

}



.modal-window {
    border: 2px solid #F8D24E;
    border-radius: 10px;
    position: relative;
    
    width: 100%;
    background-color: var(--white-color);

   
    @media screen and (min-width:768px) {
        max-width: 584px;
        max-height: 644px;
        padding: 32px 54px;
        }
   
}

.modal-window__title {
    font-family: Seymour One;
    font-weight: 700;
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.03em;

    color: #212121;
       margin-bottom: 12px;

       @media screen and(max-width:767px) {
        margin-top: 28px;
       }
}

.modal-window__list {
 

    @media screen and(min-width:320px) and(max-width:767px) {
min-width: 280px;
    align-items: center;

        display: flex;
            flex-direction: column;
            
    }
  
    @media (min-width:768px) {
    display: flex;
        gap: 32px;
        margin-top: 100px;
        margin-bottom: 40px;
    }
   
}

// .modal-window__item:not(:last-child){
// margin-bottom: 20px;
// }

.modal-window__card {
  

    position: relative;
    width: 132px;
    height: 135px;
 

    &--first {
        @media (max-width:767px) {
            margin-top: 83px;
        }

        border: 2px solid #96AF8C;
        filter: drop-shadow(0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
        border-radius: 15.7808px;
     
        
    }

    &--second {
        @media (max-width:767px) {
                margin-top: 83px;
            }
        background: #EFD478;
        border: 2px solid #EFD478;
        box-shadow: 0px 5.26027px 19.726px rgba(212, 20, 67, 0.1);
        border-radius: 15.7808px;
    }

    &--third {
        @media (max-width:767px) {
                margin-top: 83px;
                margin-bottom: 24px;
            }
        border: 2px solid #D6936D;
        filter: drop-shadow(0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
        border-radius: 15.7808px;
    }
}

.modal-window__background {
    position: absolute;

    bottom: 60px;
    left: -2px;
    width: 132px;
    height: 131px;

    border-radius: 50%;
    z-index: 1;

    &--first {
       
        background: linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)), #96AF8C;
        box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
    }


    &--second {
        
        background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
        box-shadow: 0px 5.26027px 19.726px rgba(153, 114, 23, 0.25);
    }

    &--third {
        @media (max-width:767px) {
                margin-top: 136px;
            }
        background: linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)), #D9D9D9;
        box-shadow: 0px 5.26027px 19.726px rgba(0, 0, 0, 0.25);
    }
}

.product-img {
    position: absolute;
       
    bottom: 67px;
       z-index: 2;
     
}

.product-text {
    padding-top: 90px;
    padding-bottom: 20px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.25;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: #131313;
}

.form-field {
    display: flex;
    flex-direction: column;
    position: relative;
@media screen and(min-width:768px) {
    &:not(:first-child) {
            margin-top: 16px;
        }
}
 @media screen and(max-width:767px) {
    &:not(:first-child) {
            margin-top: 12px;
        }
 } 


}



.form-label {
    display: none;
}

.form-input {
margin-left: auto;
    margin-right: auto;

    @media screen and(max-width:767px) {
        width: 224px;
        height: 32px;
    }

    @media screen and(min-width:768px) {
        width: 368px;
        height: 40px;
    }

   
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    padding-left: 16px;
    padding-top: 11px;
    padding-bottom: 11px;
    outline: transparent;
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
    border-radius: 4px;
    cursor: pointer;

}



.form-input.area {
@media screen and(max-width:767px) {
    height: 64px;
}

    @media screen and(min-width:768px) {
        height: 84px;
    }
   
    resize: none;
    padding-left: 16px;
    padding-top: 11px;

    outline: transparent;
  

}


/* -----------btn-------------- */
.submit-button {
    display: block;
    margin-right: auto;
    margin-left: auto;
margin-top: 24px;
margin-bottom: 24px;

   @media screen and(min-width:768px) {
    margin-top: 32px;
    
   }
   
    
}
