.products {
  background-image: url('../images/background/icecream-7.jpg');
  background-size: auto 1045px;

  @media screen and (min-width: 768px) {
    background-size: auto 959px;
  }
  @media screen and (min-width: 1200px) {
    background-size: auto 1013px;
  }
}

.products {
  padding: 60px 0;

  @media screen and (min-width: 768px) {
    padding-bottom: 82px;
  }
  @media screen and (min-width: 1200px) {
    padding-bottom: 66px;
  }
}

.uppertitle {
  margin-bottom: 12px;

  font-weight: 700;
  font-size: 18px;
  line-height: 1.22;

  text-align: center;
  text-transform: uppercase;

  color: var(--color-black-main);

  @media screen and (min-width: 768px) {
    font-weight: 800;
    font-size: 22px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
  }
}

.title {
  font-family: var(--secondary-font);
  font-weight: 400;
  font-size: 30px;
  line-height: 1.27;
  text-align: center;
  letter-spacing: var(--lt-sp-main);

  text-transform: uppercase;

  color: var(--color-black-main);

  @media screen and (min-width: 768px) {
    font-size: 34px;
    line-height: 1.26;

    letter-spacing: 0.06em;
  }

  @media screen and (min-width: 1200px) {
    font-size: 36px;
    line-height: 45px;
  }
}

.products__title {
  margin-bottom: 148px;

  @media screen and (min-width: 768px) {
    margin-bottom: 200px;
  }

  @media screen and (min-width: 1200px) {
    margin-bottom: 300px;
  }
}

.products__list {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 16px;
  }

  @media screen and (min-width: 1200px) {
    gap: 32px;
  }
}

.products__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  &:not(:last-child) {
    @media screen and (max-width: 767px) {
      margin-bottom: 164px;
    }
  }
}

.products__card {
  position: relative;
  padding: 123px 40px 20px;

  display: flex;
  flex-direction: column;
  align-items: center;

  width: 280px;
  height: 420px;

  border-radius: 24px;

  @media screen and (min-width: 768px) {
    padding: 108px 26px 20px;

    width: 224px;
    height: 428px;

    border-radius: 16px;
  }

  @media screen and (min-width: 1200px) {
    padding: 166px 63px 60px;

    width: 370px;
    height: 535px;
  }

  &--ice-cream {
    background-color: var(--color-green-secondary);
  }

  &--ice-coffee {
    background-color: var(--color-yellow-secondary);
  }

  &--milkshakes {
    background-color: var(--color-brown-main);
  }
}

.products__card-box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  align-items: center;

  margin-top: 40px;

  @media screen and (min-width: 1200px) {
    margin-top: 65px;
  }
}

.products__card-title {
  font-weight: 700;
  font-size: 20px;
  line-height: 1.2;
  letter-spacing: var(--lt-sp-main);

  text-align: center;
  text-transform: uppercase;

  color: var(--color-black-main);

  @media screen and (min-width: 768px) {
    font-size: 22px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 30px;
  }
}

.products__card-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.78;
  letter-spacing: var(--lt-sp-main);

  text-align: center;

  color: var(--color-black-main);

  @media screen and (min-width: 1200px) {
    font-size: 16px;
  }
}

.arrow-two {
  padding: 0;
  height: 15px;

  color: var(--color-black-main);
  background-color: transparent;
  border: none;
}

.products__background {
  position: absolute;

  top: -23%;
  width: 200px;
  height: 200px;

  background-position: top 25px center;

  border-radius: 50%;

  @media screen and (min-width: 1200px) {
    top: -28%;
    width: 300px;
    height: 300px;
  }

  &--ice-cream {
    background: linear-gradient(
        0deg,
        rgba(156, 190, 143, 0.7),
        rgba(156, 190, 143, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.34219px 20.0332px rgba(10, 136, 23, 0.25);
  }
  &--ice-coffee {
    background: linear-gradient(
        0deg,
        rgba(248, 210, 78, 0.7),
        rgba(248, 210, 78, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.40397px 20.2649px rgba(153, 114, 23, 0.25);
  }

  &--milkshakes {
    background: linear-gradient(
        0deg,
        rgba(213, 117, 63, 0.7),
        rgba(213, 117, 63, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.41333px 20.3px rgba(0, 0, 0, 0.25);
  }
}

.products__image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  align-items: center;
  translate: (0 -5%);

  @media screen and (min-width: 1200px) {
    width: 300px;
    height: 300px;
  }
}
